import './star.css'

export default function Star({starts, r}) {
    const generateStars = () => {
        const ret = [];
        for (let i = 1; i < starts; i++) {
            const curR = r + (Math.random() * 300);
            const s = 0.2 + (Math.random() * 1);
            ret.push(<div key={i} className="star" style={{
                transformOrigin: `0 0 ${curR}px`,
                transform: `translate3d(0,0,-${curR}px) rotateY(${Math.random() * 360}deg) rotateX(${Math.random() * -50}deg) scale(${s},${s})`
            }} />);
        }
        return ret;
    };
    return (
        <div className="stars">
            {
                generateStars()
            }
        </div>
    );
}