function App() {
    const chart = useRef(null);
    const [accountInfo, setAccountInfo] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    const [errorMessage, setErrorMessage] = useState(null);
    const [amount, setAmount] = useState(["", ""]);
    const [updateTimestamp, setUpdateTimestamp] = useState("");
    useEffect(() => {
        fetch('https://ehx8hr7sdb.execute-api.us-west-2.amazonaws.com/account')
            .then(data => data.json(), () => {
                setErrorMessage("Fetch Data error, please refresh the page.");
                return new Promise((resolve, reject) => reject());
            })
            .then(account => {
                const accountAmount = parseFloat(account.Item.amount.N);
                fetch(`https://ehx8hr7sdb.execute-api.us-west-2.amazonaws.com/fetch`)
                    // new Promise((resolve, reject) => reject()) //resolve(tempData))
                    .then(data => data.json(), () => {
                        setErrorMessage("Fetch Data error, please refresh the page.");
                        return new Promise((resolve, reject) => reject());
                    })
                    .then(data => {
                        const chartData = data.Items.map(o => ({
                            x: new Date(parseInt(o.Date.N)),
                            y: parseFloat(o.Amount.N)
                        })).sort((a, b) => b.x - a.x);
                        setAccountInfo(chartData);
                        setIsLoading(true);
                        setUpdateTimestamp(new Date().toLocaleTimeString());
                        const startAmount = chartData[chartData.length - 1].y;
                        const diff = chartData[0].y - startAmount;
                        const aprTime = 31540000000 / (chartData[0].x.getTime() - chartData[chartData.length - 1].x.getTime());
                        setAmount([`$${chartData[0].y}`, `${diff >= 0 ? '+$' : '-$'}${Math.abs(diff).toFixed(2)} (${(diff / accountAmount * 100).toFixed(2)}%)`, `(APY ${(diff / accountAmount * aprTime * 100).toFixed(2)}%) Past Week`]);
                        new Chart(chart.current, {
                            type: 'line',
                            steppedLine: 'false',
                            data: {
                                datasets: [{
                                    data: chartData,
                                    backgroundColor: 'rgba(255, 99, 132, 0.2)',
                                    borderColor: 'rgba(255, 99, 132, 1)',
                                    borderWidth: 1,
                                    lineTension: 0.2
                                }]
                            },
                            options: {
                                legend: {
                                    display: false
                                },
                                scales: {
                                    xAxes: [{
                                        type: 'time',

                                        ticks: {
                                            max: Date.now()
                                        }
                                    }],
                                    yAxes: [{
                                        type: 'linear',
                                        beginAtZero: false,
                                    }]
                                }
                            }
                        });
                    });
            });
    }, []);

    return (
        <div className="App">
            {errorMessage ?
                <div className="alert alert-danger mx-2 my-1">
                    {errorMessage}
                </div> : null
            }
            {isLoading ? null : <div className="fs-1 fw-bold mx-auto mt-5">loading</div>}
            <div className="mx-auto p-2" hidden={!isLoading} style={{maxWidth: "500px"}}>
                <div className="fs-2 fw-bold my-2">Coin Farm</div>

                <div className="text-start">
                    <section>
                        <h4>Account Info</h4>
                        <div className="rounded m-2 p-2 bg-white text-dark">
                            <div className="ms-1">
                                <div className="fs-1 fw-bold">{amount[0]}</div>
                                <div className="fs-6">{amount[1]}<span style={{fontSize: '0.8em'}}
                                                                       className="text-black-50 ms-1 fst-italic">{amount[2]}</span>
                                </div>
                            </div>
                            <div className="my-1">
                                <canvas ref={chart}/>
                            </div>
                            <div className="my-1">
                                <div
                                    className="text-black-50 fs-6 fst-italic">{`last updated: ${updateTimestamp}`}</div>
                                <div style={{fontSize: '0.8em'}}
                                     className="text-black-50 fst-italic">* APY is calculated based on
                                    the previous week performance
                                </div>
                                {/*<NavBar items={['1D', '1W', '1M']} onChange={() => {}}/>*/}
                            </div>
                        </div>
                    </section>
                    <section>
                        <h4>Management</h4>
                        <div hidden={!isLoading} className="rounded m-2 p-2 bg-white text-dark">

                            <div>
                                <span className="me-1 align-middle">Buy/Sell:</span>
                                <button type="button" className="btn btn-primary btn-sm mx-1"
                                        data-bs-toggle="modal" data-bs-target="#addressModal">Buy
                                </button>
                                <button type="button" className="btn btn-danger btn-sm" disabled>Sell</button>
                            </div>
                            <div className="text-black-50 fs-6 fst-italic mt-3">More functions will come soon!</div>
                            <div className="modal fade" id="addressModal" tabIndex="-1" aria-hidden="true">
                                <div className="modal-dialog">
                                    <div className="modal-content">
                                        <div className="modal-header">
                                            <h5 className="modal-title">stake USDC to the address</h5>
                                            <button type="button" className="btn-close" data-bs-dismiss="modal"
                                                    aria-label="Close"></button>
                                        </div>
                                        <div className="modal-body ">
                                            <img className="img-fluid rounded mx-auto d-block mb-1" src={address}
                                                 alt="0x08ddaadc5d09b329de078c739249a191974b51c0"/>
                                            <div><span className="fw-bold">USD Coin Address :</span><br/>
                                                0x08ddaadc5d09b329de078c739249a191974b51c0
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </section>
                </div>
            </div>
        </div>
    );
}

export default App;
